/** FONTS **/
/** COLORS **/
/** GLOBAL **/
html {
  font-size: 100% !important; }

body {
  line-height: 2.1rem !important;
  font-size: 1.4rem !important;
  font-weight: 400 !important; }

.home #main {
  padding-top: 0; }

html.avada-is-100-percent-template #main.width-100 {
  padding-left: 0 !important;
  padding-right: 0 !important; }

/** PAGES **/
.post-content {
  margin-right: 100px; }
  .post-content p {
    margin-bottom: 36px !important; }
  .post-content a {
    font-weight: 600; }

.avada-page-titlebar-wrapper {
  border-top: #00B1FC 24px solid;
  margin-top: 5px; }

.fusion-page-title-bar .fusion-page-title-row h1,
h1 {
  font-size: 2.3em !important;
  font-weight: 600; }

#main h2 {
  font-size: 1.7em !important;
  line-height: 2.94rem !important;
  border-bottom: #3C5EA0 3px solid;
  margin-left: -10px;
  padding-left: 10px;
  margin-bottom: 10px; }

.post-content h3, h3 {
  font-style: normal;
  font-size: 1.1em !important;
  letter-spacing: -0.01em !important;
  text-transform: uppercase;
  font-weight: 800 !important;
  margin-top: 0 !important;
  margin-bottom: 4px !important; }

.post-content h3 + ul,
.post-content h3 + ol,
.fusion-title-size-two + ul,
.fusion-title-size-two + ol,
h2 + ol, h2 + ul {
  margin-top: 0; }

ul, ol {
  margin-top: -36px;
  padding-left: 60px; }

/** Landing page **/
.landing-page-title-bar .fusion-page-title-wrapper {
  align-items: flex-end; }
  .landing-page-title-bar .fusion-page-title-wrapper h1 {
    margin-bottom: 18px; }

/** BANNER **/
.fusion-header .fusion-logo {
  margin-top: 0 !important; }

.fusion-header-v4 .fusion-header {
  padding: 20px !important; }

/** MAIN MENU **/
header .fusion-secondary-main-menu {
  background-color: #3C5EA0;
  border: none; }

.avada-menu-highlight-style-bottombar.fusion-top-header:not(.fusion-header-layout-v6) .fusion-main-menu > ul > li > .fusion-bottombar-highlight::after {
  border-top: #00B1FC 8px solid !important; }

body:not(.fusion-header-layout-v6).fusion-top-header .fusion-main-menu > ul > li:not(.fusion-menu-item-button) > a.fusion-bottombar-highlight:not(.fusion-main-menu-icon)::after {
  top: calc(50% + 0.9em) !important; }

.fusion-main-menu .fusion-dropdown-menu .sub-menu li.current-menu-item a,
.fusion-main-menu .fusion-dropdown-menu .sub-menu li a:hover {
  color: #fff; }

/** HOMEPAGE **/
.home .post-content {
  margin-right: 0; }

.fusion-title.homepage-title h1 {
  font-size: 2.2em;
  line-height: 3.78rem !important;
  color: #3C5EA0;
  font-family: "depot-new-condensed-web";
  font-weight: 500; }

.fusion-content-boxes-2 .heading .heading-link:hover .content-box-heading {
  color: #fff !important; }

.homepage-contentboxes .col-lg-4 {
  padding-left: 30px;
  padding-right: 30px; }

.homepage-contentboxes .content-box-wrapper {
  min-height: 450px;
  border: #00B1FC 1px solid; }
  .homepage-contentboxes .content-box-wrapper .heading {
    background-color: #00B1FC;
    padding: 25px 0; }
    .homepage-contentboxes .content-box-wrapper .heading .heading-link {
      float: none !important; }
    .homepage-contentboxes .content-box-wrapper .heading .content-box-heading {
      font-size: 1.2em !important;
      text-transform: uppercase;
      color: #fff;
      text-align: center; }
  .homepage-contentboxes .content-box-wrapper .content-container,
  .homepage-contentboxes .content-box-wrapper .fusion-read-more-button {
    margin-left: 10%;
    margin-right: 10%; }
  .homepage-contentboxes .content-box-wrapper .content-container {
    color: #3C5EA0 !important; }
  .homepage-contentboxes .content-box-wrapper .fusion-read-more-button {
    margin-bottom: 50px; }

.homepage-contentboxes .button-default {
  background-color: #fff;
  border-radius: unset;
  border: #C35B2A 1px solid; }
  .homepage-contentboxes .button-default .fusion-button-text {
    color: #C35B2A;
    font-family: "depot-new-condensed-web";
    font-size: 1.9em;
    font-weight: 500; }

/** FOOTER **/
footer .widget_custom_html.footer-menu-and-legal {
  text-align: right;
  color: #fff; }

footer .widget_custom_html.footer-menu-and-legal a {
  text-decoration: underline;
  color: #fff; }

.footer-menu-and-legal .footer-menu {
  float: right; }

.footer-menu-and-legal .footer-menu li {
  float: left;
  padding: 0 10px;
  border-right: #fff 1px solid; }

.footer-menu-and-legal .footer-menu li:last-child {
  border-right: none;
  padding-right: 0; }

footer .widget_custom_html.footer-menu-and-legal .footer-menu a {
  font-size: 1.1rem; }

footer .widget_custom_html.footer-menu-and-legal .footer-legal {
  font-size: 0.8em; }

footer .widget_custom_html.footer-menu-and-legal .footer-legal a {
  font-size: 1.0em; }
